<template>
    <el-dialog title="详情" :visible='true' class="edit-modal" @close="close" :modal="false" width="100%">
        <div id="json"></div>
        <div slot="footer" class="dialog-footer">
            <el-button @click="close">关闭</el-button>
        </div>
    </el-dialog>
</template>

<script>
  import service from './service'
  import 'jsoneditor/dist/jsoneditor.min.css'
  import JSONEditor from 'jsoneditor/dist/jsoneditor.min.js'

  export default {
    name: 'edit',
    data () {
      return {
        detail: {}
      }
    },
    props: {
      currRow: {
        updateType: Object,
        required: true
      },
      visible: {
        updateType: Object,
        required: true
      }
    },
    methods: {
      getDetail () {
        var params = {
          instanceId: this.currRow
        }
        return service.getConfigDetail(params).then(({data: res}) => {
          var container = document.getElementById('json')
          var options = {}
          var editor = new JSONEditor(container, options)
          window.jsonEditor = editor
          editor.set(res)
        })
      },
      close () {
        this.visible.dialog = null
      }
    },
    mounted () {
      this.getDetail()
    }
  }
</script>

<style>
    #json {
        height: 400px;
    }
</style>



