<template>
    <div class="config">
        <div class="search-form">
            <el-row :gutter="10">
                <el-col :span="6">
                    <el-input placeholder="请输入过滤条件" v-model="searchKey"></el-input>
                </el-col>
            </el-row>
        </div>
        <el-table id="gather-data-form" :data="filteredList" @row-click="rowClick" width="100%" align="right" :stripe="true" header-align="right">
            <el-table-column prop="name" label="应用"></el-table-column>
            <el-table-column label="实例">
                <template slot-scope="scope">
                    <div v-for="item in scope.row.url">
                        <span class="link" :href="item" @click="showDetail(item)">{{item}}</span>
                    </div>
                </template>
            </el-table-column>
            <el-table-column prop="refresh">
                <template slot-scope="scope">
                    <el-button type="primary" size="small" @click="refresh(scope.row)">refresh</el-button>
                </template>
            </el-table-column>

        </el-table>
        <component :is="visible.dialog" :currRow="currRow" :visible="visible" @refresh="getConfigList"></component>
    </div>
</template>

<script>
  import service from './service'
  import detail from './detail'

  export default {
    name: 'config',
    data () {
      return {
        searchKey: '',
        visible: {
          dialog: null
        },
        currRow: '',
        configList: []
      }
    },
    methods: {
      showDetail (item) {
        console.log('showDetail', this.visible, this)
        this.currRow = item
        this.visible.dialog = 'detail'
      },
      refresh (row) {
        console.log(row)
        var params = {
          name: row.name,
          ip: row.url.toString()
        }
        this.$confirm(' 此操作将进行远程配置刷新,刷新行为将使服务进行重新注册, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          service.refresh(params)
          this.$message({
            type: 'success',
            message: '刷新成功!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消刷新'
          })
        })
      },
      getConfigList () {
        return service.getConfigList().then(({data = {}}) => {
          this.configList = Object.keys(data).reduce((res, key, idx) => {
            res.push({name: key, url: data[key], index: idx + 1})
            return res
          }, [])
        })
      },
      rowClick (val) {
        console.log(val)
      }
    },
    computed: {
      'filteredList' () {
        return this.configList.filter(item => item.name.includes(this.searchKey)) || []
      }
    },
    mounted () {
      this.getConfigList()
    },
    components: {
      'detail': detail
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>


</style>



